.header {
    width: 100vw;
    height: 12vh;
    background-color: var(--backgroundColor);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1em;
  }
  
  .logo {
    width: 7em;
    cursor: pointer;
  }
  
  #menu {
    font-size: 2em;
    --ionicon-stroke-width: 48px;
    cursor: pointer;
  }
  
  @media screen and (min-width: 900px) {
    .header {
      padding: 2em 5em;
    }

    .logo {
      width: 10em;
      cursor: pointer;
    }
  }
  