.canvas{
    margin-top:1em;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content:center;
}

.canvas *{
    justify-self: center;
    cursor: pointer;
}

@media screen and (min-width: 900px) {
    .canvas{
        width: 100%;
        overflow: hidden;
    }
}

