*:focus{
    outline:none;
}

.hero{
    width: 100%;
    padding: 0 1em;
    position: relative;
}

.image-slider{
    width: 100%;
    height: 30vh;
    border-radius:25px;
    overflow: hidden;
    background-color:red;
}

.slide{
    width: 100%;
    height:100%;
    display:flex;
    align-items:center;
}

.slide .image{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.slide .image img{
    width: 100%;
    height: 100%;
    object-fit:cover;
    object-position:center;
}

.search{
    width: 70vw;
    height: 3em;
    border-radius:50px;
    display: flex;
    align-items: center;
    justify-content:space-between;
    background-color:#fff;
    position: absolute;
    top: 30vh;
    left: 50%;
    transform: translate(-50%,-50%);
    padding:1em;
    box-shadow:  0px 5px 60px rgba(84, 84, 84,0.5);
}

ion-icon{
    font-size:1.5em;
    color: var(--fontColor);
}

.search-box{
    border: none;
    background: transparent;
    width: 80%;
}

/* Keywords */

.keywords{
    width: 100%;
    height: 10vh;
    margin: 2em 0 0 0;
    display: flex; 
    align-items: center;
    overflow:scroll;
    
}
.keywords::-webkit-scrollbar {
    display: none;
  }

.keyword{
    width:fit-content;
    height: 5vh;
    background-color: #fff;
    padding: 0 1em;
    display: flex;
    justify-content: center;
    align-items:center;
    border-radius: 25px;
    margin-right:0.5em;
    flex-shrink: 0;
    box-shadow:  0px 3px 10px rgba(84, 84, 84,0.2);
}


@media screen and (min-width:900px){

    .hero{
        width: 100%;
        padding: 0 1em;
        position: relative;
    }
    
    .image-slider{
        width: 80%;
        height: 60vh;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
    }

    .search{
        width: 50vw;
        top: 60vh;
    }
    
    .slide .image img{
        height: 100%;
        object-fit:cover;
        object-position: center;
    }

    .keywords{
        width: 80%;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
    }

    .keywords .keyword{
        cursor: pointer;
    }

}