.product-page{
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    min-height: 100vh;
    justify-content:space-between;
}

.product{
    padding: 1em;
    padding-top: 2em;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.m-slider{
    width: 100%;
    height:30vh;
    border-radius:25px;
    overflow: hidden;
    border-radius:25px;
}

.image-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  width: 100%;
  margin: 1em;
}

.image-wrap img{
    width: 100%;
}

.controls-wrapper {
    width: 90%;
  }

/* Description */

.details{
    width: 100%;
    height: fit-content;
    padding: 1em 1.5em;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 0.5em;
}

#details{
    text-align: justify;
    color:var(--fontColor);
}

.breed{
    width: 80vw;
    padding: 0.5em 1em;
    display:flex;
    align-items:center;
    justify-content:center;
    border-radius: 50px;
    background-color:#fff;
    box-shadow:  0px 5px 60px rgba(84, 84, 84,0.5);
    margin-bottom: 1em;
}

#dog{
    width:1.5em;
}

#breed{
    margin-left:0.5em;
}

.contact{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2em;
}

.contact a{
    text-decoration:none;
}

#whatsapp, #email{
    width: 70vw;
    height:10vh;
    display: flex;
    align-items: center;
    justify-content:space-between;
    border-radius:25px;
    padding: 0 2em;
    color: #fff;
}

#whatsapp{
    background-color:#0BBF59;
}

#email{
    margin-top: 1em;
    background-color: #D92525;
}

#whatsapp ion-icon, #email ion-icon{
    color: #fff;
}

/* square buttons */
.rec.rec-arrow {
    border-radius: 0;
}
/* round buttons on hover */
.rec.rec-arrow:hover {
    border-radius: 50%;
}
/* hide disabled buttons */
.rec.rec-arrow:disabled {
    visibility: hidden;
}
/* disable default outline on focused items */
/* add custom outline on focused items */
.rec-carousel-item:focus {
    outline: none;
    box-shadow: inset 0 0 1px 1px lightgrey;
}


@media screen and (min-width:900px){
    .m-slider{
        width:100%;
        height: 60vh;
    }

    .image-wrap{
        height: 50vh;
        width: 100%;
        margin: 1em;
      }

    #whatsapp, #email{
        width: 30vw;
    }

    .breed{
        width: 40vw;
    }

}