.card{
    width:40vw;
    height: min-content;
    background-color: #fff;
    border-radius: 25px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1em;
    padding: 0.5em 0;
}

.card img{
    width:90%;
    margin: 0;
    border-radius:25px;
}

.desc{
    text-decoration: none;
    font-size:0.9em;
    color:black;
    text-align: center;
    margin: 0 1em;
    margin-top: 0.5em;
}

@media screen and (min-width:900px){
    .card{
        width: 20vw;
        padding: 1em 0;
    }
}