.contact-container{
    width:100vw;
    height: 20vh;
    position: relative;
    bottom: 0;
    background-color: #fff;
    display:flex;
    align-items: center;
    flex-direction: column;
    padding: 1em;
    flex-shrink: 0;
}

#c-title{
    color: var(--fontColor);
    margin-top: -0.5em;
}

#c-title::after{
    content:" ";
    width:35%;
    height: 0.15em;
    background-color: var(--backgroundColor);
    position:absolute;
    top:1.5em;
    left: 50%;
    transform: translateX(-50%);
}

.social{
    width: 50%;
    display:flex;
    align-items: center;
    justify-content:space-evenly;
    margin-top: 1em;
    color: #242424;
}

#phone{
    width: 50%;
    display:flex;
    align-items: center;
    margin-top: 1em;
    justify-content: space-evenly;
}

#phone ion-icon{
    font-size:1em;
}


@media screen and (min-width:900px){
    .contact-container *{
        cursor: pointer;
    }
    .social{
        width: 20%;
    }

    #phone{
        width: 15%;
    }
    .social *{
        font-size: 1.5em;
    }
}